import React from "react";
import cl from './FormsControls.module.css'
import {required} from "../../../utils/validators/validators";
import {Field} from "redux-form";


const FormConrol = ({input, meta: {touched, error}, child, ...props}) => {
    const hasError = touched && error;
    return (
        <div className={cl.formControl + ' ' + (hasError ? cl.error : ' ')}>
            <div>
                {props.children}
            </div>
            {hasError && <span>{error}</span>}
        </div>
    )
};

export const Textarea = (props) => {
    const {input, meta, child, ...restProps} = props;
    return <FormConrol {...props}><textarea {...input} {...restProps}/></FormConrol>
};

export const Input = (props) => {
    const {input, meta, child, ...restProps} = props;
    return <FormConrol {...props}><input {...input} {...props}/></FormConrol>
};

export const createField = (placeholder, name, validators, component, type) =>
    <div>
        <Field placeholder={placeholder}
               type={type} name={name}
               validate={validators}
               component={component}/>
    </div>;
